.dashboard-card {
  border-radius: 10px;
  padding: 10px 15px;
  max-width: 300px;
  background: rgba(135, 135, 135, 0.11);
  .card-top {
    font-size: 12px;
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    >span {
      margin-bottom: 10px; }
    button {
      margin-bottom: 10px;
      width: 26px;
      height: 26px;
      display: flex;
      padding: 0;
      position: relative;
      align-items: center;
      justify-content: center;
      border: 1.8px solid transparent;
      transition: 0.3s ease;
      border-radius: 50%;
      &:hover {
        border-color: $accent;
        img {
          filter: brightness(0) invert(1); }
        &::before {
          opacity: 1; } }
      &::before {
        position: absolute;
        display: block;
        content: '';
        width: 18px;
        height: 18px;
        opacity: 0;
        transition: 0.3s ease;
        background: red;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        border-radius: 50%; }
      img {
        position: relative;
        z-index: 2;
        width: 12px;
        height: 12px; } } }

  .plan-name {
    font-size: 16px;
    font-family: $semibold; }

  .included-items-wrapper {
    border: 1px solid #787878;
    border-right: none;
    border-left: none;
    display: flex;
    margin: 10px 0;
    .included-item {
      text-decoration: none;
      display: flex;
      flex-direction: column;
      align-items: center;
      transition: 0.3s ease;
      &.inactive:hover {
        transform: scale(1.1);
        filter: grayscale(0);
        opacity: 1; } }
    .img-wrapper {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center; }
    .item-name {
      font-size: 9px !important;
      text-align: center;
      color: red;
      line-height: 1.5; } }
  .plan-type-wrapper {
    width: 100%;
