    align-items: center;
    span {
      font-size: 18px;
      text-transform: uppercase; } }
  .total-wrapper {
    span {
      font-size: 14px;
      &:last-child {
        font-family: $semibold;
        font-style: italic; }
      strong {
        font-size: 26px; } } }
  .plan-time {
    >div {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between; }
    span {
      font-size: 12px;
      color: #787878; } } }



