.my-profile {
  width: 130px;
  display: flex;
  align-items: center;
  position: relative;
  &.active {
    .profile-img {
      background: $accent;
      color: #ffffff;
      img[alt="arrow-down"] {
        filter: invert(1); } } }
  &:hover {
    .profile-img {
      background: $accent;
      color: #ffffff;
      img[alt="arrow-down"] {
        filter: invert(1); } }
    .profile-actions {
      opacity: 1;
      pointer-events: unset; } } }

.profile-img {
  display: flex;
  position: relative;
  z-index: 10;
  width: 100%;
  height: 30px;
  background: transparent;
  border: 1px solid $accent;
  color: #000;
  border-radius: 15px;
  align-items: center;
  justify-content: space-evenly;

  img[alt="arrow-down"] {
    filter: invert(0); }
  span {
    font-size: 14px;
    font-family: $semibold;
    line-height: 1; } }

.user-img-wrapper {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 100%;
    height: 100%; } }

.profile-actions {
  pointer-events: none;
  opacity: 0;
  transition: 0.3s ease;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 110px;
  background: #fff;
  border: 2px solid $accent;
  border-radius: 15px 15px 5px 5px; }

.inner-actions-wrapper {
  margin-top: 36px;
  padding: 0 15px 10px 15px;
  a {
    color: #000000;
    font-size: 14px;
    text-decoration: none;
    display: flex;
    align-items: center;
    .img-wrapper {
      margin-right: 9px;
      width: 16px;
      height: 16px;
      display: flex;
      align-items: center;
      justify-content: center; } }

  .line {
    margin: 5px 0;
    background: #878787; } }

.account-menu {
  a {
    .img-wrapper {
      width: 80px;
      height: 45px;
      display: flex;
      align-items: center;
      justify-content: center; }
    h4 {
      font-size: 16px;
      font-family: $semibold;
      border: none !important; } } }

.profile-balance {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  p {
    font-size: 17px;
    font-family: $semibold;
    font-style: italic;
    margin-bottom: 7px;
    span {
      font-size: 18px;
      color: #787878;
      font-style: normal; }
    strong {
      font-size: 31px; } } }

.recharge-link {
  background: $accent;
  color: #fff;
  font-size: 12px;
  transition: 0.3s ease;
  border: none;
  &:hover {
    text-decoration: none;
    background: $darkerred;
    color: #fff; } }



@media screen and ( max-width: 992px ) {
  .profile__card {
    max-width: 250px;
    margin-right: auto;
    margin-left: auto; }
  .account-menu {
    margin-bottom: 0 !important;
    width: 100% !important; } }


@media screen and ( max-width: 450px ) {
  .profile-actions {
    height: 100px; }
  .account-menu.red-hovered-nav {
    margin-top: -15px !important;
    .line {
      margin-top: 5px;
      margin-bottom: 20px; }
    a {
      .img-wrapper {
        width: 60px;
        height: 35px;
        img {
          max-width: 100%;
          max-height: 100%; } }
      h4 {
        font-size: 12px;
        font-family: $semibold;
        border: none !important; } } }
  .my-profile {
    width: 100px;
    margin-right: auto;
    margin-left: unset !important;
    .profile-img {
      height: 25px; }
    span {
      font-weight: normal;
      font-family: $default-font;
      font-size: 12px;
      + img {
        width: 8px;
        height: 8px; } }
    .user-img-wrapper {
      width: 15px;
      height: 15px; } }
  .profile-header {
    >.row {
      flex-direction: column-reverse;
      .profile-balance {
        transform: translateY(-30px);
        margin: 0 !important;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-direction: row;
        p {
          font-size: 10px;
          margin-bottom: 7px;
          span {
            font-size: 12px; }
          strong {
            font-size: 17px; } } }
      .recharge-link {
        width: 12px;
        height: 12px;
        font-size: 0;
        border-radius: 50%;
        padding: 0;
        margin-left: 5px;
        background-image: url('../img/icons/arrow-right.svg');
        background-size: 35%;
        background-position: 55% center;
        background-repeat: no-repeat; } } } }

