.footer__top {
  background: $accent;
  padding: 30px 0; }

.footer__navbar {
  max-width: 960px;
  margin: 0 auto;
  .nav-item {
    .nav-link {
      font-size: 18px; } }

  .download-app {
    border: 2px solid #ffffff;
    border-radius: 15px;
    transition: 0.3s ease;

    &:hover {
      background: #fff;
      a {
        color: $accent !important; } } } }

.footer__line {
  height: 2px;
  background: #fff; }

.footer__contact {
  width: 95%;
  margin: 0 auto;
  justify-content: space-around !important;
  .contact-link {
    display: block;
    padding: 5px 10px;
    border: 2px solid #ffffff;
    transform: skewX(-20deg);
    border-radius: 10px;
    line-height: 1;
    transition: 0.3s ease;

    &:hover {
      background: #fff;
      span {
        color: $accent; } }

    span {
      display: block;
      transform: skewX(20deg);
      font-weight: bold;
      font-size: 22px; } }
  a {
    font-size: 17px;
    font-family: $semibold;
    text-decoration: none; }

  img {
    margin-right: 10px;
    margin-top: 5px; }

  span.text-white {
    font-size: 17px;
    font-family: $semibold;
    span {
      font-size: 14px; } } }


.footer__bottom {
  padding: 30px 0;
  background: #B20000;

  .footer__bottom-line {
    width: 2px;
    height: 14px;
    margin: 0 5px;
    margin-top: 3px;
    background: #fff; }

  .footer-el-1 {
    .small-text {
      margin-right: 5px;
      display: block;
      font-size: 12px; }
    a {
      color: #ffffff;
      text-decoration: underline;
      font-size: 12px; } }
  .footer-el-2 {
    a {
      img {
        filter: invert(1); } } } }

#appStoreModal {
  margin-top: 100px; }

@media (max-width: 576px) {
  .footer__contact {
    width: 100%;
    justify-content: space-between !important; }
  .footer__top {
    .navbar-nav {
      width: 100% !important; } }
  .contact-link {
    margin-bottom: 20px; }
  .footer-el-3 {
    margin-right: auto;
    margin-left: auto; }
  .footer__bottom {
    padding: 15px 0; } }

@media (max-width: 400px) {
  .contact-link {
    margin-right: auto;
    margin-left: auto; } }

